.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 50px;
    background-color: #fff;
    animation: headerAppear 1s ease-out forwards;
    position: relative;
    z-index: 1;
  }
  
  @keyframes headerAppear {
    0% {
      opacity: 0;
      transform: translateY(-50px);
    }
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .header__logo {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }
  
  .header__logo-img {
    height: 55px;
  }
  
  .header__nav {
    font-family: "Rubik", serif;
    color: rgba(0, 0, 0, 0.7216);
    display: flex;
    gap: 90px;
  }
  
  .header__nav a {
    text-decoration: none;
    color: #333;
    font-size: 23px;
  }
  
  .header__actions {
    display: flex;
    gap: 30px;
  }
  
  .header__download {
    border: none;
    font-size: 16px;
    cursor: pointer;
    background-color: #4A90E2;
    font-family: "Rubik", serif;
    font-weight: bold;
    color: #fff;
    box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.47);
    width: 135px;
    height: 50px;
  }
  
  .header__login img {
    height: 40px;
  }
  
  .header__login {
    align-items: center;
    gap: 10px;
    display: flex;
  }
  
  .header__login p {
    font-family: "Rubik", serif;
    font-size: 21px;
  }
  
  .button_down {
    display: flex;
    align-items: center;
  }
  
  .still_name {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    position: relative;
  }
  
  #still {
    cursor: pointer;
    font-size: 20px;
    color: #333;
    width: 100px;
  }
  
  .button_still {
    display: flex;
    flex-direction: column;
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 0px 20px 4px #9595959c;
    padding: 15px;
    margin-top: 10px;
    border-bottom: 2px solid black;
  }
  
  .button_still a {
    text-decoration: none;
    color: #333333;
    font-size: 18px;
    padding: 5px 0;
  }

  .img_down {
    height: 45px;
    display: none;
  }

  .hamburger-menu {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 30px;  /* Ширина полосок */
    height: 25px; /* Высота контейнера */
    cursor: pointer;
  }
  
  .bar {
    width: 100%;
    height: 5px;  /* Толщина полосок */
    background-color: #333; /* Цвет полосок */
    border-radius: 3px;
    display: none;
  }

  .hamb-menu {
    width: 300px;
    display: none;
    padding-left: 30px;
  }
  
  
  @media (max-width: 1490px) {
    .header__nav a {
      font-size: 21px;
    }
  
    .header__login p {
      font-size: 19px;
    }
  
    .header__nav {
      gap: 70px;
    }
  }
  
  @media (max-width: 1300px) {
    .header__login p {
      display: none;
    }
  
    .header__login img {
      height: 35px;
    }
  
    .header__download {
      font-size: 15px;
      width: 120px;
      height: 40px;
    }
  
    .header__actions {
      gap: 20px;
    }
  
    .header {
      border-bottom: 1px solid #e0e0e0;
    }
  
    .still_name p {
      margin-top: 0px;
    }
  }

  @media (max-width: 1119px) {
    .accept {
        display: none;
    }

    .header {
        padding: 10px 25px
    }

    .header__nav {
        align-items: center;
        gap: 65px;
        margin-left: 30px;
    }

    .header__actions {
        width: 200px;
        display: flex;
        justify-content: center;
    }

    .header__download {
        display: none;
    }

    .img_down {
        display: block;
    }

    #still {
        margin-bottom: 0px;
    }

    .header__actions {
        gap: 15px;
    }
  }
  
  @media (max-width: 999px) {
    .developers {
      display: none;
    }
  }
  
  @media (max-width: 940px) {
    .header__nav {
        gap: 50px;
    }

    .header {
        padding: 10px 20px;
    }

    @media (max-width: 895px) {
        .updates, .support, .still_name {
            display: none;
        }

        .bar {
            display: block;
            width: 100%;
            height: 5px;
            background-color: #333;
            border-radius: 3px;
        }

        .hamb-menu {
            display: flex;
        }

        .hamburger-menu {
            padding-left: 30px;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            width: 30px;
            height: 25px;
            cursor: pointer;
            position: relative;
        }


        .hamb-menu {
            position: relative;
            transition: width 0.3s ease;
        }

        .menu-dropdown {
            display: flex;
            flex-direction: column;
            position: absolute;
            justify-content: center;
            margin-top: 50px;
            background-color: rgb(255, 255, 255);
            box-shadow: 0px 0px 20px 4px #9595959c;
            text-decoration: none;
        }

        .menu-dropdown a {
            text-decoration: none;
            color: #4A90E2;
        }
        
    }
  }
